import React,{Component} from 'react';

export default class Search extends Component{
	
	render(){
		return (
				<section id="search" className="alt">
					<form method="post" action="#">
						<input type="text" name="query" id="query" placeholder="Search" />
					</form>
				</section>
		);
	}


}


